let React = Serverboards.React
let ReactDOM = Serverboards.ReactDOM
let rpc = Serverboards.rpc

import SQLTextInput from './sqltextinput'

function main([el], settings){
  console.log(el, settings)
  const service=settings.service

  function Console(props){
    return (
      <div className="ui container">
        <h2 className="ui header">SQL Console for <i>{service.name}</i></h2>
        <SQLTextInput/>
      </div>
    )
  }

  Serverboards.ReactDOM.render(React.createElement(Console, null), el)

  return function(){
    Serverboards.ReactDOM.unmountComponentAtNode(el)
  }
}

Serverboards.add_screen("serverboards.remotesql/console", function(el,co){ setTimeout(function(){ main(el,co) }, 1000) })
